import React from 'react';

const Tag = ({
  setScreen,
  selectedTag,
  selectTag,
  activities,
  updateActivity,
  setActivities,
  tags,
}) => {
  const {
    id,
    code,
    title,
  } = selectedTag;
  return (
    <div className="screen">
      <button onClick={() => setScreen(1)}>&larr; Назад</button>
      <h1>{title} ({code}, id={id})</h1>
      <div>
        <button onClick={() => selectTag({
          ...tags[id - 1],
          id: id - 1,
        })} disabled={id === 0}>&larr;</button>
        <button onClick={() => selectTag({
          ...tags[id + 1],
          id: id + 1,
        })} disabled={id === tags.length - 1}>&rarr;</button>
      </div>
      {activities
        .map((activity, index) => ({ ...activity, index }))
        .sort((a, b) => a.title > b.title ? 1 : -1)
        .map((activity) => (
        <label className="tag" key={`${activity.index}${activity.code}`}>
          <input type="checkbox" checked={(activity.tags || []).includes(code)} onChange={() => {
            const activityId = activity.tableData?.id || activity.index;
            let newTags = activity.tags || [];
            if (newTags.includes(code)) {
              newTags = newTags.filter(t => t !== code);
            } else {
              newTags.push(code);
            }
            updateActivity({
              ...activity,
              tags: newTags,
            }, activityId)
            activities[activityId].tags = newTags;
            setActivities([...activities]);
          }} />
          {activity.title}
        </label>
      ))}
    </div>
  );
};

export default Tag;
