import React, { useEffect, useState } from 'react';
import "./App.css";
import Header from "./Header";
import Table from "./Table"
import firebase from "firebase/compat/app";
import { getAuth, GoogleAuthProvider, onAuthStateChanged, signInWithPopup } from "firebase/auth";
import { getDatabase, ref, child, get, set } from "firebase/database";
import "firebase/compat/auth";
import Activity from './Activity';
import Tag from './Tag';

const firebaseConfig = {
  apiKey: 'AIzaSyCExrlbwm2DMX_lrhS516-93vUf4GSPgAA',
  authDomain: "tryeverything-421ed.firebaseapp.com",
  databaseURL: "https://tryeverything-421ed-default-rtdb.europe-west1.firebasedatabase.app",
  projectId: "tryeverything-421ed",
  storageBucket: "tryeverything-421ed.appspot.com",
  messagingSenderId: "768977457633",
  appId: "1:768977457633:web:df590da29170678cdf42a0",
  measurementId: "G-56RPZMD413"
};

let auth, provider;

if (firebase.apps.length === 0) {
  const app = firebase.initializeApp(firebaseConfig);
  auth = getAuth(app);
  provider = new GoogleAuthProvider();
}


const db = getDatabase();
const dbRef = ref(db);

const addActivity = (activity, newID) => {
  set(ref(db, 'activities/' + newID), activity).catch(error => {
    alert(error);
  });
}

const updateActivity = (updatedActivity, id) => {
  set(ref(db, 'activities/' + id), updatedActivity).catch(error => {
    alert(error);
  });
}

const addTag = (tag, newID) => {
  set(ref(db, 'tags/' + newID), tag).catch(error => {
    alert(error);
  });
}

const updateTag = (updatedTag, id) => {
  set(ref(db, 'tags/' + id), updatedTag).catch(error => {
    alert(error);
  });
}

const App = () => {
  const [activities, setActivities] = useState([]);
  const [tags, setTags] = useState([]);
  const [screen, setScreen] = useState(1);
  const [authorizedUser, setAuthorizedUser] = useState({});
  const [selectedActivity, setSelectedActivity] = useState(null);
  const [selectedTag, setSelectedTag] = useState(null);

  const getAppData = () => {
    Promise.all([
      get(child(dbRef, '/activities/')),
      get(child(dbRef, '/tags/')),
    ]).then(([activitiesSnapshot, tagsSnapshot]) => {
      setActivities(activitiesSnapshot && activitiesSnapshot.val());
      setTags(tagsSnapshot && tagsSnapshot.val());
    });
  }

  useEffect(() => {
    onAuthStateChanged(auth, (user) => {
      if (user) {
        setAuthorizedUser(user);
        getAppData();
      } else {
        signInWithPopup(auth, provider)
          .then(({ user }) => {
            setAuthorizedUser(user);
            getAppData();
          }).catch((error) => {
            console.log(error);
          });
      }
    });
  }, []);

  // if (activities.length === 0 || tags.length === 0) {
  //   return <div></div>;
  // }

  const selectActivity = (activity) => {
    setScreen(2);
    setSelectedActivity(activity);
  };

  const selectTag = (tag) => {
    setScreen(3);
    setSelectedTag(tag);
  };

  return (
    <div>
      <Header user={authorizedUser} />
      {screen === 1 ? (
        <div>
          <Table
            title="Категории"
            data={tags}
            setData={setTags}
            addItem={addTag}
            updateData={updateTag}
            selectRow={selectTag}
          />
          <Table
            title="Активности"
            data={activities}
            setData={setActivities}
            addItem={addActivity}
            updateData={updateActivity}
            selectRow={selectActivity}
            hasCode={false}
          />
        </div>
      ) : null}
      {screen === 2 && <Activity
        setScreen={setScreen}
        selectedActivity={selectedActivity}
        selectActivity={selectActivity}
        activities={activities}
        updateActivity={updateActivity}
        setActivities={setActivities}
        tags={tags}
      />}
      {screen === 3 && <Tag
        setScreen={setScreen}
        selectedTag={selectedTag}
        selectTag={selectTag}
        activities={activities}
        updateActivity={updateActivity}
        setActivities={setActivities}
        tags={tags}
      />}
    </div>
  )
};

export default App;
